.diagram-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.diagram-bar__inner {
  height: 100%;
  width: 100%;
  border: 1px solid #313131;
  border-radius: 12px;
  background-color: #EFE9E1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.diagram-bar__fill {
  background-color: #949ab1;
  width: 100%;
  transition: all 0.3s ease-out;
}

.diagram-bar__label {
  font-weight: bold;
  font-size: 0.7rem;
  text-align: center;
  margin-bottom: 5px; 
}
